/* src/styles/tipnix.css */
.tipnix {
  --background: #7c7c7c;
  --color: #e8e8e8;
  position: relative;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
  font-size: 14px;
  font-weight: 500;
  color: var(--color);
  border-radius: 8px;
  text-transform: uppercase;
  height: auto;
  width: max-content;
  display: grid;
  place-items: center;
  margin-top: 400px;
  background-color: var(--background);
}

.tipnix-tooltip {
  position: absolute;
  width: 225px;
  top: 0;
  left: 50%;
  padding: 10px;
  opacity: 0;
  pointer-events: none;
  transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
  background: var(--background);
  z-index: 100;
  border-radius: 8px;
  transform-origin: 0 0;
  text-transform: capitalize;
  font-weight: 400;
  font-size: 16px;
  box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
}

.tipnix-tooltip::before {
  position: absolute;
  content: '';
  height: 0.6em;
  width: 0.6em;
  bottom: var(--tooltip-before-bottom, -0.2em);
  top: var(--tooltip-before-top, -0.2em);
  left: var(--tooltip-before-left, 0);
  right: var(--tooltip-before-right, unset);
  transform: translate(-50%) rotate(45deg);
  background: var(--background);
}

.tipnix:hover .tipnix-tooltip {
  top: -100%;
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  scale: 1;
}

.tipnix:hover {
  box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
  color: white;
  border-color: transparent;
}

.tooltip-above::before {
  top: unset !important;
  bottom: -0.2em !important;
}

.tooltip-below::before {
  top: -0.2em !important;
  bottom: unset !important;
}

@keyframes shake {
  0% {
    rotate: 0;
  }

  25% {
    rotate: 7deg;
  }

  50% {
    rotate: -7deg;
  }

  75% {
    rotate: 1deg;
  }

  100% {
    rotate: 0;
  }
}

@keyframes bounce {
  0%,
  100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }
}

@keyframes fadeSlideUp {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes zoom {
  0% {
    transform: scale(0.8);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes pulse {
  0%,
  100% {
    transform: scale(1);
    opacity: 1;
  }

  50% {
    transform: scale(1.1);
    opacity: 0.8;
  }
}

@keyframes flipIn {
  0% {
    transform: rotateX(-90deg);
    opacity: 0;
  }

  100% {
    transform: rotateX(0deg);
    opacity: 1;
  }
}

@keyframes slideInLeft {
  0% {
    transform: translateX(-20px);
    opacity: 0;
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes swing {
  0% {
    transform: rotate(0deg);
  }

  20% {
    transform: rotate(15deg);
  }

  40% {
    transform: rotate(-10deg);
  }

  60% {
    transform: rotate(5deg);
  }

  80% {
    transform: rotate(-5deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

@keyframes fadeInScale {
  0% {
    opacity: 0;
    transform: scale(0.8);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes tiltFadeIn {
  0% {
    opacity: 0;
    transform: rotate(-10deg) translateY(10px);
  }

  100% {
    opacity: 1;
    transform: rotate(0deg) translateY(0);
  }
}
